import React from 'react'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'

import BreadcrumbNav from '../components/breadcrumb-nav'
import TabNav from '../components/tab-nav.js'
import Layout from '../components/layout'
import MyJumbotron from '../components/jumbotron'
import {
  flattenFileNode,
  matterNameFromMatter,
  classeNameFromClasse,
  classeWikidataURIFromClasse,
} from '../helpers'

import 'katex/dist/katex.min.css'

export default exoPage

function exoPage({ data, location }) {
  const exo = flattenFileNode(data.markdownRemark)

  const title = `Exercices de ${classeNameFromClasse(
    exo.classe
  )} en ${matterNameFromMatter(exo.matter)}: ${exo.chapter}`

  const description = `Exercices de ${classeNameFromClasse(
    exo.classe
  )} en ${matterNameFromMatter(exo.matter)} intitulé: «${exo.chapter}»`

  // type
  const type = exo.type

  // chemin vers le cours pour référence
  const currentCoursePath = location.pathname
    .substring(0, location.pathname.length - type.length)
    .replace(/^\/|\/$/g, ``)

  return (
    <Layout location={location} data={data}>
      <Helmet title={`${title}`} htmlAttributes={{ class: `exo` }}>
        <script type="application/ld+json">{`
         {"@context": "http://schema.org/",
          "@type": "exercice",
          "name": "${exo.part} | ${exo.chapter}",
          "description": "${description}",
          "learningResourceType": [
            "lesson"    ],
          "provider": {
            "@type": "schema:EducationalOrganization",
            "name": "lyceum.fr"
          },
          "audience": {
            "@type": "EducationalAudience",
            "éducationRole": "student"
          },
          "educationalAlignment": {
            "@type": "AlignmentObject",
            "alignmentType": "educationalLevel",
            "educationalFramework": "French Grade Levels",
            "targetName": "${classeNameFromClasse(exo.classe)}",
            "@id": "${classeWikidataURIFromClasse(exo.classe)}"
          },
          "url": "${location.href}"
        }
    `}</script>
      </Helmet>
      <BreadcrumbNav node={exo} />
      <TabNav currentCoursePath={currentCoursePath} type={type} />
      <MyJumbotron>
        <h1>Exercices</h1>
        <strong>{`Chapitre ${exo.nChapter}: ${exo.chapter}`}</strong>
      </MyJumbotron>
      <div dangerouslySetInnerHTML={{ __html: exo.html }} />
    </Layout>
  )
}

export const query = graphql`
  query MDExoQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      fields {
        classe
        chapter
        matter
        nChapter
        part
        nPart
        type
      }
    }
  }
`
